* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: montserrat, sans-serif;
  }
  
  header {
    background-color: #FFCE00;
    background-image: linear-gradient(to right,  rgba(70, 85, 223, 0.753), #0004ff);
    box-shadow: 0px 3px 6px rgba(0,0,0,0.2);
    padding: 15px;
  }

  header h1 {
    display: inline-block;
    color: #F3F3F3;
    font-size: 32px;
    font-weight: 600;
    font-style: italic;
    text-transform: uppercase;
    background: none;
    border: none;
    outline: none;
    appearance: none;
  }
  
  header .subreddit_input {
    display: inline-block;
    color: #F3F3F3;
    font-size: 32px;
    font-weight: 600;
    font-style: italic;
    text-transform: uppercase;
    background: none;
    border: none;
    outline: none;
    appearance: none;
  }
  
  header:before {
    content: "/r/";
    display: inline-block;
    color: #F3F3F3;
    font-size: 32px;
    font-weight: 600;
    font-style: italic;
    text-transform: uppercase;
    text-align: center;
    height: 150px;
  }
  
  article {
    padding: 30px 15px;
    background-color: #FFF;
    color: #212121;
  }
  
  article a {
    text-decoration: none;
  }
  
  article h3 {
    color: #212121;
  }
  
  article:nth-child(2n) {
    background-color: #F3F3F3;
  }
  
  article:hover {
    background-image: linear-gradient(to left,  rgba(70, 85, 223, 0.753), #0004ff);
  }
  
  article:hover h3 {
    color: #FFF;
  }

  .favMode article {
    padding: 30px 15px;
    background-color: #FFF;
    color: #212121;
  }
  
  .favMode article a {
    text-decoration: none;
  }
  
  .favMode article h3 {
    color: #212121;
  }
  
  .favMode article:nth-child(2n) {
    background-color: #F3F3F3;
  }
  
  .favMode article:hover {
    background-image: linear-gradient(to left, rgba(70, 85, 223, 0.753), #0004ff);
  }
  
  .favMode article:hover h3 {
    color: #FFF;
  }

  .post 
  {
    display: flex;
  }

  #like {
    height: 25px;
    width: 25px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    margin-right:2%;
   }

   #like:hover {
     height: 25px;
     width: 25px;
     background-color: #FE4880;
     border-radius: 50%;
     display: inline-block;
    }

    #like:active {
      height: 25px;
      width: 25px;
      background-color: #FFCE00;
      border-radius: 50%;
      display: inline-block;
     }

     #options {
      background-color: #FFF;
      color: #212121;
     }

     #options button {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      font-family: montserrat, sans-serif;
      width: 100%;
     }